'use client';

interface ProductCardProps {
  id: string;
  name: string;
  price: number;
  originalPrice?: number;
  image: string;
  rating: number;
  sold: number;
  onAddToCart: () => void;
}

export default function ProductCard({
  id,
  name,
  price,
  originalPrice,
  image,
  rating,
  sold,
  onAddToCart,
}: ProductCardProps) {
  const discount = originalPrice ? Math.round(((originalPrice - price) / originalPrice) * 100) : 0;

  return (
    <div className="bg-white rounded-lg shadow hover:shadow-lg transition-shadow overflow-hidden">
      {/* Product Image */}
      <div className="relative w-full h-48 bg-gray-200 overflow-hidden">
        <img src={image} alt={name} className="w-full h-full object-cover hover:scale-110 transition-transform" />
        {discount > 0 && (
          <div className="absolute top-2 right-2 bg-red-500 text-white px-2 py-1 rounded text-sm font-bold">
            -{discount}%
          </div>
        )}
      </div>

      {/* Product Info */}
      <div className="p-4">
        <h3 className="font-semibold text-gray-800 line-clamp-2 mb-2 h-14">{name}</h3>

        {/* Price */}
        <div className="mb-2">
          <span className="text-lg font-bold text-red-500">₫{price.toLocaleString()}</span>
          {originalPrice && (
            <span className="text-sm text-gray-400 line-through ml-2">
              ₫{originalPrice.toLocaleString()}
            </span>
          )}
        </div>

        {/* Rating and Sold */}
        <div className="flex items-center gap-2 mb-3 text-sm text-gray-600">
          <span>⭐ {rating}</span>
          <span>| Đã bán {sold}</span>
        </div>

        {/* Add to Cart Button */}
        <button
          onClick={onAddToCart}
          className="w-full bg-red-500 hover:bg-red-600 text-white font-semibold py-2 rounded-lg transition-colors"
        >
          Thêm vào giỏ
        </button>
      </div>
    </div>
  );
}
